/**
 * 
 * @param {string} pathname 
 * @returns {string} 
 */
export const getRealPathName = (pathname: string) => {
  try {
    let realPathName = pathname;
    const paths = realPathName.split('/');
    //
    for (let index = 0; index < paths.length; index++) {
      const path = paths[index];
      if (path === 'micro-app' && paths.length > 3) {
        //
        realPathName = `/${paths[index + 2]}`;
        break;
      } else if (path === 'micro-app-control' && paths.length > 2) {
        //
        realPathName = `/${path}/${paths[index + 1]}`;
        break;
      }
    }

    return realPathName;
  } catch (error) {
    return pathname;
  }
};

/**
 * 
 * @param {any} defaultConfig 
 * @returns {string} 
 */
export const getProjectRelativePath = (defaultConfig?: any) => {
  try {
    let relativePath = './';
    // 
    if (defaultConfig && defaultConfig.hasOwnProperty('defaultPath'))
      relativePath = defaultConfig.defaultPath;
    //  
    return relativePath;
  } catch (error) {
    return './';
  }
};
/**
 * script
 * @param {string} url 
 * @param {function} callback 
 * @returns
 */
export const loadJS = (url: string, callback: any) => {
  try {
    let script: any = document.createElement('script'),
      fn = callback || function () {};
    script.type = 'text/javascript';
    //IE
    if (script.readyState) {
      script.onreadystatechange = function () {
        if (
          script.readyState === 'loaded' ||
          script.readyState === 'complete'
        ) {
          script.onreadystatechange = null;
          fn();
        }
      };
    } else {
      //
      script.onload = function () {
        fn();
      };
    }
    script.src = url;
    document.getElementsByTagName('head')[0].appendChild(script);
  } catch (error) {}
};
/**
 * script
 * @param srcUrl
 */
export const delScriptTag = async (srcUrl) => {
  try {
    let script = document.querySelector(`script[src='${srcUrl}']`);
    script && script.remove();
  } catch (error) {}
};
/**
 * link
 * @param {string} url 
 * @param {function} callback 
 * @returns
 */
 export const loadLink = (url:string, callback:any) => {
  try {
    let link: any = document.createElement('link'),
      fn = callback || function () {};
    link.rel = 'stylesheet';
    link.type = 'text/css';
    //IE
    if (link.readyState) {
      link.onreadystatechange = function () {
        if (link.readyState === 'loaded' || link.readyState === 'complete') {
          link.onreadystatechange = null;
          fn();
        }
      };
    } else {
      //
      link.onload = function () {
        fn();
      };
    }
    link.href = url;
    document.getElementsByTagName('head')[0].appendChild(link);
  } catch (error) {}
};
/**
 *  undefined
 * @param {object} obj 
 * @param {string} key 
 * @returns {any} 
 */
export const tryGet = (obj: object, key: string) => {
  return key.split('.').reduce(function (o:any, x) {
    return typeof o == 'undefined' || o === null ? o : o[x];
  }, obj);
};
/**
 * 
 * @param {string|number} code 
 * @returns {boolean} 
 */
 export const verifyCode = (code: any) => {
  try {
    return code === 200 || code === '200' || code === '0';
  } catch (error) {
    return false;
  }
};
/**
 * 
 * @param {object} data 
 * @returns
 */
 export const errorMessage = (data:any) => {
  return typeof data === 'object'
    ? data.code && data.message && !verifyCode(data.code)
      ? `${data.code}${data.message}`
      : data.message
    : data;
};
/**
 * URL
 * @param url
 * @param key
 * @returns
 */
 export const getQueryString = (url:string, key:string) => {
  if (url && url.indexOf('?') > -1 && key) {
    const search = url.split('?')[1];
    const regExp = new RegExp(`(^|&)${key}=([^&]*)(&|$)`);
    const values = search.match(regExp);
    if (values != null) return decodeURIComponent(values[2]);
  }
  return null;
};