import React from 'react';
import { Navigate, Route, Routes, useLocation } from 'react-router-dom';
import BasicLayout from './basicLayout';
import Login from '../pages/login';
import menus from '../routes';

const AuthRouter = () => {
  const location = useLocation();

  const token: string = localStorage.getItem('token') || '1';

  // 
  if (token && location.pathname === '/login') {
    return <Navigate to="/" replace />;
  }

  // 
  const onEnterApp = () => {
    // Token 
    //  Token  mobx Token
    //  Locastorage  Token 
    if (!token) {
      return <Navigate to="/login" replace />;
    }
    return <BasicLayout />;
  };

  //  menus 
  const renderRoutes = (routes: any) => {
    return routes.map((item: any) => {
      // 
      const Component = item.component;
      // 
      return item.children?.length ? (
        <React.Fragment key={item.path}>
          {/*      */}
          <Route
            path={item.path}
            element={
              item.redirect && location.pathname === item.path ? (
                <Navigate to={item.redirect} />
              ) : null
            }
          />
          <Route path={item.path}>{renderRoutes(item.children)}</Route>
        </React.Fragment>
      ) : (
        <React.Fragment key={item.path}>
          {/*  Navigate  */}
          <Route
            path={item.path}
            element={
              item.redirect ? <Navigate to={item.redirect} /> : <Component />
            }
          />
        </React.Fragment>
      );
    });
  };
  //  404 
  const NotFound = () => {
    return <div></div>;
  };
  return (
    <Routes>
      <Route path="/login" element={<Login />}></Route>
      <Route element={onEnterApp()}>
        {renderRoutes(menus)}
        <Route path="*" element={<NotFound />}></Route>
      </Route>
    </Routes>
  );
};

export default AuthRouter;
